import React from "react";
import PropTypes from "prop-types";

export default function DateTime({ dateTimeStr }) {
  const dateTime = Date.parse(dateTimeStr);
  let timeDesc;
  if (Number.isNaN(dateTime)) return <div />;
  /*
   x
   x
   xxx
  xxx
  */
  const now = Date.now();
  const timeCap = now - dateTime;
  const oneMinute = 60 * 1000;
  const oneHour = 60 * oneMinute;
  const oneDay = 24 * oneHour;
  if (timeCap < oneHour) {
    timeDesc = `${Math.round(timeCap / oneMinute)}`;
  } else if (
    timeCap < oneDay &&
    new Date(now).getDate() === new Date(dateTime).getDate()
  ) {
    timeDesc = `${Math.round(timeCap / oneHour)}`;
  } else if (new Date(now).getFullYear() === new Date(dateTime).getFullYear()) {
    timeDesc = `${new Date(dateTime).getMonth()}${new Date(
      dateTime
    ).getDate()}`;
  } else {
    timeDesc = `${new Date(dateTime).getFullYear()}${new Date(
      dateTime
    ).getMonth()}${new Date(dateTime).getDate()}`;
  }

  return timeDesc;
}
DateTime.propTypes = {
  dateTimeStr: PropTypes.string.isRequired
};
