import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import MakeSettingPanel from "./MakeSettingPanel";

const data = user => ({
  title: "",
  list: [
    {
      key: 1,
      title: `@${user.name}`,
      list: [
        {
          key: 1,
          type: "link",
          to: "/settings/account",
          title: "",
          isNav: true
        },
        {
          key: 2,
          type: "link",
          to: "/settings/safety",
          title: "",
          isNav: true
        },
        {
          key: 3,
          type: "link",
          to: "/settings/notifications",
          title: "",
          isNav: true
        },
        {
          key: 4,
          type: "link",
          to: "/settings/content_preferences",
          title: "",
          isNav: true
        }
      ]
    },
    {
      key: 2,
      title: "",
      list: [
        {
          key: 1,
          type: "link",
          to: "/settings/data",
          title: "",
          isNav: true
        },
        {
          key: 2,
          type: "link",
          to: "/settings/accessibility",
          title: "",
          isNav: true
        },
        {
          key: 3,
          type: "link",
          to: "/settings/about",
          title: "",
          isNav: true
        }
      ]
    }
  ]
});

function SettingPanel({ user }) {
  return <MakeSettingPanel data={data(user)} />;
}
SettingPanel.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired,
    phone: PropTypes.number.isRequired,
    email: PropTypes.string.isRequired,
    language: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired
  }).isRequired
};
const mapStateToProps = state => ({
  user: state.currentUser
});
export default connect(mapStateToProps)(SettingPanel);
