import React from "react";
import { Route, withRouter } from "react-router-dom";
import styled from "styled-components";
import PropTypes from "prop-types";
import ReactRouterPropTypes from "react-router-prop-types";
import { connect } from "react-redux";
import Layout from "../layout/Layout";
import Account from "./Account";
import Safety from "./Safety";
import { useMediaQuery } from "../utilitys";
import SubTitle from "./SubTitle";
import LinkItem from "./LinkItem";
import BackHeadWithUsername from "../middleComponents/BackHeadWithUsername";

const Container = styled.div`
  background-color: rgb(230, 236, 240);
  height: 100%;
  margin-bottom: 59px;
`;
function Settings({ user, match, history }) {
  const isWide = useMediaQuery("(min-width: 1000px)");
  if (isWide) {
    history.push("/settings/account");
  }
  return (
    <Layout
      head={<></>}
      main={(
<Container>
  <BackHeadWithUsername title="" />
  <SubTitle>
@{user.name}</SubTitle>
  {[
            { url: "settings/account", title: "" },
            { url: "settings/safety", title: "" },
            { url: "settings/notifications", title: "" },
            { url: "settings/content_preferences", title: "" }
          ].map(({ title, url }) => (
            <LinkItem key={title} to={url} title={title} />
          ))}
  <SubTitle></SubTitle>
  {[
            { url: "settings/data", title: "" },
            { url: "settings/accessibility", title: "" },
            { url: "settings/about", title: "" }
          ].map(({ url, title }) => (
            <LinkItem key={title} to={url} title={title} />
          ))}
</Container>
)}
      rightAside={(
<>
  <Route path={`${match.url}/account`} component={Account} />
  <Route path="/settings/safety" component={Safety} />
</>
)}
    />
  );
}
Settings.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired
  }).isRequired,
  match: ReactRouterPropTypes.match.isRequired,
  history: ReactRouterPropTypes.history.isRequired
};
const mapStateToProps = state => ({
  user: state.currentUser
});
export default withRouter(connect(mapStateToProps)(Settings));
